<script>
import VerifyInfo from '@/components/verifyInfo'
import EnterCardNo from '@/components/enterCardNo'
import BindCardSuccess from '@/components/bindCardSuccess'
import BindCardFailed from '@/components/bindCardFailed'
import QueryPage from '@/components/queryPage'
let ERR_OK = 'success'; //
export default {
  name: 'addBankCard',
  data () {
    return {
      isAuthorization: this.isAuthorization ? this.isAuthorization:'', //
      protocol:false, //
      bankprotocol:false, //
      isBindCardPay:'', //
      orderInfo:false,  //
      // tradeTime: '',  //
      productName:'',//
      companyName:'',//
      orderId: '',   //
      amount: '',//
      isShowBankTip:false, //
      isShowMask: false,  //
      bankCardInfo:[], //  [{"bankCode":"ABC","bankName":"ff","cardType":"","number":"2218"}]
      isCreditCard:true,  //
      placeholder: '',
      required: false, //
      verifyName: '', //name
      smsCode: '', //
      check: true,//
      nopOrderId: '',  //
      requestFlowId: '' , //
      contextPath:contextPath  //
    }
  },
  watch:{
    smsCode(){
      if(this.smsCode.length > 6){
          this.smsCode = this.smsCode.substring(0,6);
      }
    }
  },
  created() {
    // this.tradeTime = tradeTime;
    this.orderId = orderId;
    this.amount = amount;
    this.productName = productName;
    this.companyName = companyName;
    this.isBindCardPay = token //
    var bindCardInfo = JSON.parse(localStorage.getItem("bindCardInfo"));
    if(bindCardInfo == null || !bindCardInfo || bindCardInfo == 'undefined'){
      //
        alert("");
        return false;
    }
    if(bindCardInfo.cardType == '2'){
      this.isCreditCard = true;  //
    }else{
      this.isCreditCard = false;
    }
    this.bankCardInfo.push(bindCardInfo)
    // localStorage.removeItem("bindCardInfo")//     
  },
  mounted() { 
    User.form.validate("#form");
  },
  methods:{
    showProtocal() {
      // 
      var self = this;
      self.isShowMask = true;
      self.protocol = true;
    },
    showBankProtocal() {
      //  
      var self = this;
      self.isShowMask = true;
      self.bankprotocol = true;
    },
    close() {
      var self = this;
      self.isShowMask = false;
      self.protocol = false;
      self.check = true;
    },
    closeBank() {
      var self = this;
      self.isShowMask = false;
      self.bankprotocol = false;
      self.check = true;
    },
    getVerifyCode(e) {  //
      var self = this;
      self.required = false;//
      self.verifyName='';//name
      self.$nextTick(function () {   //dom
        var result = User.form.autoValidate("#form");
        var target = e.target;
        if(result){
          self.placeholder ='';
          var cardInfo = User.form.serialize("#form");
          var cardno = JSON.parse(localStorage.getItem("addItem")).cardno;
          if(cardno == null || cardno == 'undefined'){
              alert("");
              return false;
          }
          User.form.disabledSubmit("#getCode");   //
          if(self.isAuthorization == ''){   //
            var param = User.convertJson(cardInfo,bindCardMerchantRequest);
            param.token = token;
            param.cardno = cardno;
            self.$http.get(Request.url+'/bindCard/authBindCard',{params: param}).then((res) => {
              User.form.enabledSubmit("#getCode");   //
              var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
              if(data.bizStatus == ERR_OK){  //
                User.autoValidateCode(target);//
                self.placeholder ='';
                self.nopOrderId = data.authBindRequest.nopOrderId;
                self.requestFlowId = data.authBindRequest.requestFlowId;
                localStorage.nopOrderId = self.nopOrderId;
                localStorage.requestFlowId = self.requestFlowId;
                self.isAuthorization ='fail'; 
              }else{
                self.isShowMask = true;
                Tip.titleContentBtn('',data.errormsg,function(){
                  dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                    self.isShowMask = false;
                    dom.querySelector("#tip1").classList.add("none");
                  },false);
                })
                if(data.smsStatus == 'smsSendError'){  //
                  self.isAuthorization ='fail';
                }else{
                  self.isAuthorization ='';//
                }
              }
            });
          }else if(self.isAuthorization == 'fail'){
            var param = bindCardMerchantRequest;
            param.token = token;
            param.nopOrderId = self.nopOrderId;
            param.requestFlowId = self.requestFlowId;
            self.$http.get(Request.url+'/bindCard/authBindCardSMS',{params: param}).then((res) => {
              var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
              User.form.enabledSubmit("#getCode");   //
              if(data.bizStatus == ERR_OK){  //
                  User.autoValidateCode(target);//
              }else{
                self.isShowMask = true;
                var smsStatus = data.smsStatus;
                Tip.titleContentBtn('',data.errormsg,function(){
                  dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                    self.isShowMask = false;
                    dom.querySelector("#tip1").classList.add("none");
                  },false);
                })
                if(smsStatus == 'smsSendError'){  //
                    User.autoValidateCode(target);//
                }else if(smsStatus == 'smsNotPermitError'){  //
                  self.isShowMask = true;
                  Tip.titleContentBtn('','',function(){
                    dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                      self.isShowMask = false;
                      dom.querySelector("#tip1").classList.add("none");
                    },false);
                  })
                  return false;
                } 
                return false;
              }
            });
          }
        }
      })
    },
    surePay() {  //
      var self = this;
      self.required = true;//
      self.verifyName = 'smsCode';//name smsCode
      self.$nextTick(function () {   //dom
        var result = User.form.autoValidate("#form");
        if(result){
          User.form.disabledSubmit("#submit");
          var cardInfo = User.form.serialize("#form");
          var param = User.convertJson(cardInfo,bindCardMerchantRequest);  //bindCardMerchantRequest:object
          param.token = token;
          param.smsCode = self.smsCode;
          param.nopOrderId = self.nopOrderId;
          param.requestFlowId = self.requestFlowId;
            self.$http.get(Request.url+'/bindCard/authBindCardConfirm',{params: param}).then((res) => {
              var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
              User.form.enabledSubmit("#submit");
              switch(data.bizStatus){
                case 'bindPaySuccess': //
                  location.assign(self.contextPath+"/wap/query/result?token="+token);
                break;
                case 'bindPayFail':  //
                  if(data.smsStatus == "smsVerifyError" || data.smsStatus == "smsInputError" || data.smsStatus == "smsNotGetError"){
                      //smsNotGetError  
                      //smsVerifyError   smsInputError
                      self.isShowMask = true;
                      Tip.titleContentBtn('',data.errormsg,function(){
                        dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                          self.isShowMask = false;
                          dom.querySelector("#tip1").classList.add("none");
                        },false);
                      })
                      self.isAuthorization == 'fail';
                  }else{
                    var errormsg = data.errormsg;
                    var errorcode = data.errorcode;
                    var needRepay = data.needRepay;
                    if(needRepay){
                      location.assign(self.contextPath+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg+"&needRepay="+needRepay);
                    }else{
                      location.assign(self.contextPath+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg);
                    }
                  }
                break;
                case 'bindPayNeedItem': // 
                  var cardNumber = JSON.parse(localStorage.getItem("addItem"));
                  if(cardNumber == null || cardNumber == undefined){
                      alert('');
                      return false;
                  }
                  var needSurportDTO = data.needBankCardItem.needSurportDTO;  //
                   ////
                  var cardInfo2  = {};  //
                  for(var item in data.needBankCardItem){
                    if(typeof data.needBankCardItem[item] == 'string'){
                      cardInfo2[item] = data.needBankCardItem[item];
                    }
                  }
                  ////
                  var smsType = data.smsType;
                  var addItem = User.convertJson(cardNumber,cardInfo2); 
                  if(smsType){  //
                    localStorage.smsType = data.smsType;
                  }
                  localStorage.bindId = data.authBindConfirm.bindId;
                  localStorage.setItem("addItem",JSON.stringify(addItem));
                  localStorage.setItem("needSurportDTO",JSON.stringify(needSurportDTO));
                  self.$router.push({name:'VerifyInfo', path: '/VerifyInfo', component:VerifyInfo})
                break;
                case 'bindPayUnknown':  //
                   location.assign(self.contextPath+"/query_index.vm");
                break;
                case 'bindCardFail':  //
                  if(data.smsStatus == "smsVerifyError" || data.smsStatus == "smsInputError" || data.smsStatus == "smsNotGetError"){
                      //smsNotGetError  
                      //smsVerifyError   smsInputError
                      self.isShowMask = true;
                      Tip.titleContentBtn('',data.errormsg,function(){
                        dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                          self.isShowMask = false;
                          dom.querySelector("#tip1").classList.add("none");
                        },false);
                      })
                      self.isAuthorization == 'fail';
                  }else{
                    self.$router.push({name:'BindCardFailed', path:'/BindCardFailed', component:BindCardFailed});//
                  }
                break;
                case 'bindCardSuccess':  //
                  localStorage.frontCallBackUrl = data.frontCallBackUrl;
                  self.$router.push({name:'BindCardSuccess', path:'/BindCardSuccess', component:BindCardSuccess});//
                break;
                case 'bindCardUnKnown':  //
                  self.$router.push({name:'QueryPage', path:'/QueryPage', component:QueryPage});//
                break;
              }
            });
        }
      })
    },
    showBankTip() { //
      var self = this;
      self.isShowMask = true;
      self.isShowBankTip = true;
    },
    goTo() {
      this.$router.push({ name:'EnterCardNo', path: '/', component: EnterCardNo})
    },
    hiddenBankTip() {  //
      var self = this;
      self.isShowMask = false;
      self.isShowBankTip = false;
    },
    noSubmit(event){
      event.preventDefault();
      return false;
    }
  }
}
</script>

