import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import EnterCardNo from '@/components/enterCardNo'   //
import AddBankCard from '@/components/addBankCard'  //
import BindCardSuccess from '@/components/bindCardSuccess'   //
import BindCardFailed from '@/components/bindCardFailed'   //
import QueryPage from '@/components/queryPage'   //
import VerifyInfo from '@/components/verifyInfo'  //

Vue.use(Router)
Vue.use(Resource)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'EnterCardNo',   //
      component: EnterCardNo
    },
    {
      path: '/AddBankCard',  //
      name: 'AddBankCard',
      component: AddBankCard
    },
    {
      path: '/BindCardSuccess',  //
      name: 'BindCardSuccess',
      component: BindCardSuccess
    },
    {
      path: '/BindCardFailed',  //
      name: 'BindCardFailed',
      component: BindCardFailed
    },
    {
      path: '/QueryPage',  //
      name: 'QueryPage',
      component: QueryPage
    },
    {
      path: '/VerifyInfo',  //
      name: 'VerifyInfo',
      component: VerifyInfo
    } 
  ]
})
export default router;



