<template>
  <div class="verifyInfo">
    <div class="wrapper b-transparent">
      <form class="bt bgfff fs16 mt10" id="form" name="form" @submit="noSubmit($event)">
        <label v-if="this.needSurportDTO.cardnoIsNeed">
          <div class="info li pr">
            <mark class="dis-inline text c333 pl12"></mark>
            <input class="required input-text" name="cardno" encrypt="true" type="text" maxlength="23" placeholder="">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <label v-if="this.needSurportDTO.ownerIsNeed">
          <div class="info li pr">
            <mark class="dis-inline text c333 pl12">&nbsp;&nbsp;&nbsp;</mark>
            <input class="required input-text" name="owner" encrypt="true" type="text" placeholder="">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <label v-if="this.needSurportDTO.idnoIsNeed">
          <div class="info bt li pr">
            <mark class="dis-inline text c333 pl12"></mark>
            <input class="required input-text" name="idno" encrypt="true" maxlength="22" type="text" placeholder="">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <label v-if="this.needSurportDTO.phoneNoIsNeed">
          <div class="info bt li pr">
            <mark class="dis-inline text c333 pl12"></mark>
            <input class="required input-text" name="phoneNo"  encrypt="true" maxlength="11" type="text" placeholder="">
            <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
        <div v-if="this.isCreditCard" class="creditCard">
          <label v-if="this.needSurportDTO.avlidDateIsNeed">
            <div class="info bt li pr">
              <mark class="dis-inline text c333 pl12"></mark>
              <input class="required input-text" name="avlidDate" encrypt="true" type="text" maxlength="4" placeholder="/(09/15 0915)">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <label v-if="this.needSurportDTO.cvvIsNeed">
            <div class="info li pr bt">
              <mark class="dis-inline text c333 pl12">C V V 2</mark>
              <input class="required input-text" name="cvv" type="text" encrypt="true" maxlength="3" placeholder="">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
        </div>
          <label v-if="this.needSurportDTO.idCardTypeIsNeed">
            <div class="info bt li pr">
              <mark class="dis-inline text c333 pl12"></mark>
              <input class="required input-text" name="idCardType" encrypt="true" placeholder="">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <label v-if="this.needSurportDTO.bankPWDIsNeed">
            <div class="info bt li pr">
              <mark class="dis-inline text c333 pl12"></mark>
              <input class="required input-text" name="bankPWD" maxlength="6" encrypt="true" type="password" placeholder="">
              <i class="del pa icon" @click="delInfo($event)">&#xe65d;</i>
            </div>
          </label>
          <label v-if="this.verify">
            <div class="info bt li pr verifyBox">
              <mark class="dis-inline text c333 pl12"></mark>
              <input class="input-text verifyCode" :class="{required:this.required}" maxlength="6" :name="this.verifyName" type="text" :placeholder="placeholder">
              <button class="fs14 tc getCode pa" id="getCode" @click="getVerifyCode" type="button"></button>
            </div>
          </label>
      </form>
      <!-- <p class="fs12 c999 pl12 mt10">****{{phoneEndNo}}</p> -->
      <div class="pl12 pr12">
        <button class="mt30 w" :class="{dis:check}" id="submit" @click="surePay" :disabled="check" type="button"></button>
      </div>
    </div>
    <!-- <footer class="tc fs14 c999">{{cardType}}</footer> -->
    <div class="bg-fixed" v-show="isShowMask" id="mask"></div>
  </div>
</template>

<script>
let ERR_OK = 'success'; //
// import PaySuccess from '@/components/paySuccess.vue'
export default {
  name: 'verifyInfo',
  data () {
    return {
      isShowBankTip:false, //
      isShowMask: false,  //
      // phoneEndNo: '', //
      isCreditCard: true, //
      placeholder: '',
      required: false, //
      verifyName: '', //name
      addItem: null, //
      needSurportDTO: {
        "cardnoIsNeed":'',
        "ownerIsNeed":'',
        "idnoIsNeed":'',
        "phoneNoIsNeed":'',
        "cvvIsNeed":'',
        "avlidDateIsNeed":'',
        "idCardTypeIsNeed":'',
        "bankPWDIsNeed":''
      }, //
      // idCardType: '',// 
      idCardTypeVal: '',// 
      verify:'',//
      bankPWDVal: '',// ,
      bindId:'',// Id 
      check:true //
    }
  },
  created() {
    var self = this;
    self.addItem = JSON.parse(localStorage.getItem("addItem"));//
    if(localStorage.getItem("needSurportDTO") != 'undefined'){
      self.needSurportDTO = JSON.parse(localStorage.getItem("needSurportDTO"));//
    }
    self.bindId = localStorage.bindId; // Id
    self.verify = localStorage.smsType;//
    if(self.addItem == null || self.addItem == undefined){
        alert('');
        return false;
    }
    if(!self.verify){
      self.check = false;
    }
  },
  mounted(){
    var self = this;
  },
  methods:{
    getVerifyCode(e) {  //
      var self = this;
      self.required = false;//
      self.verifyName='';//name
      self.$nextTick(function () {   //dom
        var result = User.form.autoValidate("#form");
        var target = e.target;
        if(result){
          self.placeholder =''; 
          var formItem = User.form.serialize("#form");
          var param = User.convertJson(self.addItem,formItem); 
          param.token = token;
          param.bindId = this.bindId;
          param.smsType = 'YEEPAY';
          User.form.disabledSubmit("#getCode");
          self.$http.get(Request.url+'/wap/ajax/bindSmsSend',{params: param}).then((res) => {
            User.form.enabledSubmit("#getCode");
            var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
            self.placeholder ='';
            if(data.status != 'success'){
              self.isShowMask = true;
              Tip.titleContentBtn('',data.msg,function(){
                dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
                  self.isShowMask = false;
                  dom.querySelector("#tip1").classList.add("none");
                },false);
              })
            }
            User.autoValidateCode(target);//
            self.check= false;
          });
        }
      })
    },
    surePay() {  //
      var self = this;
      self.required = true;//
      self.verifyName = 'verifycode';//nameverifyCode
      self.$nextTick(function () {   //dom
        var result = User.form.autoValidate("#form");
        if(result){
          // var param = self.addItem;
          var formItem = User.form.serialize("#form");
          var param = User.convertJson(self.addItem,formItem); 
          param.token = token;
          param.bindId = self.bindId;
          param.firstBindCardPay = true;
          User.form.disabledSubmit("#submit");
          self.$http.get(Request.url+'/wap/ajax/pay/bind',{params: param}).then((res) => {
            User.form.enabledSubmit("#submit");
            var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
            if(data.bizStatus == ERR_OK){
              location.assign(Request.url+"/wap/query/result?token="+token);
            }else{
              var errormsg = data.errormsg;
              var errorcode = data.errorcode;
              var needRepay = data.needRepay;
              if(needRepay){
                location.assign(Request.url+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg+"&needRepay="+needRepay);
              }else{
                location.assign(Request.url+"/wap/fail?token="+token+"&errorCode="+errorcode+"&errorMsg="+errormsg);
              }
            }
          });
        }
      })
    },
    noSubmit(event){
      event.preventDefault();
      return false;
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.wrapper{
  .dis{background: #ccc;}
  min-height: 94vh;
  #form{
    label{
      display: block;
      height:44px;
      mark{
       display: inline-block;
        width: 73px;
        height: 100%;
        line-height: 44px;
        background-color: transparent;
        text-align: justify;
        overflow: hidden;
      }
      mark:after {
          content: " ";
          display: inline-block;
          width: 100%
      }
    }
  }
  .info{
    width:100%;
    height:44px;
    line-height: 44px;
    &.verifyBox{
      .errMsg{
        right:88px;
      }
    }
    .text{
      width:72px;
      margin-right: 14px;
    }
    .input-text{
      width:calc(100%-40px);
      line-height: 40px;
      vertical-align: top;
      &.verifyCode{height: 44px;}
    }
    .resendMessage{
      background: #f0f0f0;
      color:#666;
      height:32px;
      line-height: 32px;
      top:4px;
      right:12px;
    }
    .getCode{
      width:92px;
      height:32px;
      line-height: 32px;
      background: #2eb944;
      color:#fff;
      top:4px;
      right:12px;
      &:active{
        background: #2ea009;
      }
    }
  }
}
</style>



